#corporate__users__table {
  table.MuiTable-root {
    thead {
      th:nth-of-type(1) {
        width: 105px !important;
        padding-left: 12px;
      }

      th:nth-of-type(2) {
        width: 70px !important;
        padding-right: 0;
      }

      th:nth-of-type(3) {

      }

      th:nth-of-type(4) {
        width: 60px !important;
      }

      th:nth-of-type(5) {
        width: 60px !important;
      }

      th:nth-of-type(6) {
        width: 60px !important;
      }
      th:nth-of-type(7) {
        width: 81px !important;
      }
      th:nth-of-type(8) {
        width: 115px !important;
      }
    }

    tbody {
      tr:hover {
        background: rgba(255, 255, 255, 0.08);
      }
      td:nth-of-type(1) {
        text-align: right;
        padding-left: 0;
      }

      td:nth-of-type(2) {
        width: 150px !important;
        padding-right: 0;
        color: rgba(255,255,255,0.6);
      }

      td:nth-of-type(3) {
        min-width:150px;
        color: rgba(255,255,255,0.6);
      }

      td:nth-of-type(4) {
        color: rgba(255,255,255,0.6);
      }

      td:nth-of-type(5) {
        color: rgba(255,255,255,0.6);
      }
      td:nth-of-type(6) {

      }

      .MuiSwitch-root {
        float: left;
      }
    }

  }
  .corporate__users__panel {

    .MuiFilledInput-input {
      padding: 27px 12px 10px;
    }
    .MuiFormControl-root {
      width: 250px;
      margin-right: 20px;
      * {
        font-weight: 400;
      }
      label {
        color:rgba(255,255,255,0.4);
        font-weight: 300;
      }
    }
  }
}