@import 'vars';
// ==================== TYPOGRAPHY ========================= //


@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400;500;700&display=swap');


* {
  font-family: 'Roboto', sans-serif;
  color: $colorDark;
}
.font--exo2 {
  font-family: 'Exo 2', sans-serif;
}

h1 {
  font-size: 36px;
  font-weight: 500;
  line-height: 0.9;
}
h2 {
  font-size: 28px;
  font-weight: 500;
  line-height: 0.9;
  font-family: 'Exo 2', sans-serif;
  color: $colorLight;
}
h3 {
  font-size: 24px;
  font-weight: 500;
  line-height: 0.9;
}

body, p, span, div {
  font-size: 16px;
  line-height: 25px;
  font-weight: 300;
}

b {
  font-weight: bold;
  font-size: inherit !important;
}

@media screen and (max-width: 767px) {
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 24px;
  }
  h3 {
    font-size: 18px;
  }
  body, p, span, div {
    font-size: 14px;
    line-height: 21px;
  }
}

// ==================== TYPOGRAPHY END ========================= //