#personal__cost__table {
  table.MuiTable-root {
    thead {
      th:nth-of-type(1) {
        width: 105px !important;
        text-align: right;
        padding-left: 0;
      }

      th:nth-of-type(2) {
        width: 150px !important;
        padding-right: 0;
      }

      th:nth-of-type(3) {
        width: 130px !important;

      }

      th:nth-of-type(4) {
        width: 45% !important;
      }

      th:nth-of-type(5) {
        width: 100px !important;
        padding-left: 10px;
        padding-right: 10px;
        text-align: right;
      }

      th:nth-of-type(6) {
        width: 140px !important;
      }
    }

    tbody {
      td:nth-of-type(1) {
        text-align: right;
        padding-left: 0;
      }

      td:nth-of-type(2) {
        width: 150px !important;
        padding-right: 0;
      }

      td:nth-of-type(3) {
        width: 10% !important;
        min-width:140px;
         color: rgba(255, 255, 255, 0.86) !important;
      }

      td:nth-of-type(4) {
        width: 35% !important;
         color: rgba(255, 255, 255, 0.86) !important;
      }

      td:nth-of-type(5) {
        width: 7% !important;
        padding-left: 10px;
        padding-right: 10px;
         color: rgba(255, 255, 255, 0.86) !important;
        text-align: right;
      }
      td:nth-of-type(6) {
        width: 13% !important;
      }
    }
  }
}