@import 'vars';
@import '_typography';
@import 'basic';
@import 'tables';
@import 'buttons';
@import 'mui';

body {
  background: #1b1f25;

}


* {
  box-sizing:border-box;
  user-select:none;
  transform-style: preserve-3d;
}

.App {
  display: flex;
  flex-direction: row;
  &-logo {
    height: 400px;
    pointer-events: none;
  }
  &-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    h2 {
      color:#61dafb7d;
    }
  }
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}




@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.config-shop, .purchased {
  max-width: 1320px;
  margin: 0 auto;
}

.config-shop .MuiTableRow-root:hover,
.purchased .MuiTableRow-root:hover  {
  background-color: #363C50;
  cursor: pointer;
}

.MuiTableCell-root.MuiTableCell-body.MuiTableCell-paddingNone {
  border-bottom: 1px solid #373B45!important;
}

.logo {
  height: 65px;
  display:flex;
  padding-left: 20px;
}
.dashboard-content {
  padding: 0 20px;
}
